* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.App {
  text-align: center
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 75ms linear;
  animation: App-logo-spin infinite 75ms linear;
  height: 5rem;
}

.App-header {
  background-color: #440044;
  height: 9rem;
  padding: 1rem;
  color: #fff
}

.App-title {
  font-size: 1.5em
}

.App-intro {
  font-size: 1rem;
  padding: 0.5rem 0;
}

.Messagebox {
  padding: 0.5rem;
  margin: 0rem auto 2rem auto;
  background: rgba(87, 164, 251, 0.4);
  color: #6f1111;
  font-weight: 600;
}



@-webkit-keyframes App-logo-spin {
  0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg)
  }

  to {
      -webkit-transform: rotate(1turn);
      transform: rotate(1turn)
  }
}

@keyframes App-logo-spin {
  0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg)
  }

  to {
      -webkit-transform: rotate(1turn);
      transform: rotate(1turn)
  }
}

@-webkit-keyframes blinkingBackground {
  0% {
      background-color: #10c018;
  }

  25% {
      background-color: #d3ff4b;
  }

  50% {
      background-color: #10c018;
  }

  75% {
      background-color: #d3ff4b;
  }

  100% {
      background-color: #10c018;
  }
}

@keyframes blinkingBackground {
  0% {
      background-color: #10c018;
  }

  25% {
      background-color: #d3ff4b;
  }

  50% {
      background-color: #10c018;
  }

  75% {
      background-color: #d3ff4b;
  }

  100% {
      background-color: #10c018;
  }
}



