.marble, .marble-empty, .marble-selected, .marble-valid-for-selection {
    background-color: #007ed8;
    border-radius: 100%;
    font-weight: 400;
    border: 1px solid #57a4fb;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
    text-align: center;
    height: 3.125rem;
    width: 3.125rem;
    margin-right: 0.625rem; /*10px*/
    text-indent: -8888px;
    -webkit-box-shadow: none;
            box-shadow: none;
}

.marble, .marble-selected {
    background-image: -webkit-radial-gradient(10% 10% at 50% 50%,hsla(0,0%,100%,.6) 50%,hsla(0,0%,100%,0)),-webkit-radial-gradient(12% 12% at 60% 25%,hsla(0,0%,100%,.6) 50%,hsla(0,0%,100%,0)),-webkit-radial-gradient(100% 100% at 60% 50%,transparent 0,rgba(0,0,0,.25) 50%);
    background-image: -o-radial-gradient(10% 10% at 50% 50%,hsla(0,0%,100%,.6) 50%,hsla(0,0%,100%,0)),-o-radial-gradient(12% 12% at 60% 25%,hsla(0,0%,100%,.6) 50%,hsla(0,0%,100%,0)),-o-radial-gradient(100% 100% at 60% 50%,transparent 25%,rgba(0,0,0,.25) 50%);
    background-image: -o-radial-gradient(10% 10% at 50% 50%,hsla(0,0%,100%,.6) 50%,hsla(0,0%,100%,0)),-o-radial-gradient(12% 12% at 60% 25%,hsla(0,0%,100%,.6) 50%,hsla(0,0%,100%,0)),-o-radial-gradient(100% 100% at 60% 50%,transparent 0,rgba(0,0,0,.25) 50%);
    background-image: radial-gradient(10% 10% at 50% 50%,hsla(0,0%,100%,.6) 50%,hsla(0,0%,100%,0)),radial-gradient(12% 12% at 60% 25%,hsla(0,0%,100%,.6) 50%,hsla(0,0%,100%,0)),radial-gradient(100% 100% at 60% 50%,transparent 0,rgba(0,0,0,.25) 50%);
}

.marble:hover {
    background-color: #1809f0
}

.marble-empty {
    opacity: 0;
    cursor: default;
}

.marble-selected {
    background-color: #f29e03;
    border-color: #f7b233;
}

.marble-valid-for-selection {
    background-color: #0be43a;
    border-color: transparent;
    -webkit-animation: blinkingBackground 2s infinite;
            animation: blinkingBackground 2s infinite;
}




